.sign {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: radial-gradient(
    ellipse 50% 10% at 50% 50%,
    #1ecfdb,
    transparent
  );
  transform: translate(-50%, -50%);
  letter-spacing: 2;
  left: 50%;
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 30px;
  color: #45cece;
  text-shadow: 0 0 0.6rem #90fbff, 0 0 1.5rem #70c0ce,
    -0.2rem 0.1rem 1rem #6db3bd, 0.2rem 0.1rem 1rem #65ffff,
    0 -0.5rem 2rem #24f0ff, 0 0.5rem 3rem #24fff4;
  -webkit-animation: shine 2s forwards, flicker 3s infinite;
          animation: shine 2s forwards, flicker 3s infinite;
}

@-webkit-keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #e6feff;
    text-shadow: 0 0 0.6rem #e6feff, 0 0 1.5rem #65ffff,
      -0.2rem 0.1rem 1rem #65f5ff, 0.2rem 0.1rem 1rem #65ffff,
      0 -0.5rem 2rem #24fff4, 0 0.5rem 3rem #24f0ff;
  }
  28%,
  33% {
    color: #65fff7;
    text-shadow: none;
  }
  82%,
  97% {
    color: #24f8ff;
    text-shadow: none;
  }
}

@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #e6feff;
    text-shadow: 0 0 0.6rem #e6feff, 0 0 1.5rem #65ffff,
      -0.2rem 0.1rem 1rem #65f5ff, 0.2rem 0.1rem 1rem #65ffff,
      0 -0.5rem 2rem #24fff4, 0 0.5rem 3rem #24f0ff;
  }
  28%,
  33% {
    color: #65fff7;
    text-shadow: none;
  }
  82%,
  97% {
    color: #24f8ff;
    text-shadow: none;
  }
}

.flicker {
  -webkit-animation: shine 2s forwards, blink 3s 2s infinite;
          animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
  -webkit-animation: shine 2s forwards, blink 10s 1s infinite;
          animation: shine 2s forwards, blink 10s 1s infinite;
}

@-webkit-keyframes shine {
  0% {
    color: #186b6b;
    text-shadow: none;
  }
  100% {
    color: #e6feff;
    text-shadow: 0 0 0.6rem #e6f9ff, 0 0 1.5rem #65e8ff,
      -0.2rem 0.1rem 1rem #65f5ff, 0.2rem 0.1rem 1rem #65e0ff,
      0 -0.5rem 2rem #24f8ff, 0 0.5rem 3rem #24f8ff;
  }
}

@keyframes shine {
  0% {
    color: #186b6b;
    text-shadow: none;
  }
  100% {
    color: #e6feff;
    text-shadow: 0 0 0.6rem #e6f9ff, 0 0 1.5rem #65e8ff,
      -0.2rem 0.1rem 1rem #65f5ff, 0.2rem 0.1rem 1rem #65e0ff,
      0 -0.5rem 2rem #24f8ff, 0 0.5rem 3rem #24f8ff;
  }
}

